<template><section><html><head></head><body><h1>HTML 元素</h1>
<p><code v-pre="">vc-overlay-html</code> 组件用于添加 HTML 标签到场景中。<strong>注意</strong> 使用此组件需要引入<code v-pre="">import 'vue-cesium/lib/style.css'</code>样式文件。参考 <a href="https://www.wellyyss.cn/ysCesium/main/app.html">ysCesium|跃焱邵隼</a> 。</p>
<h2>示例</h2>
<h3>添加 HTML 元素到场景</h3>
<h4>预览</h4>
<doc-preview>
  <template>
    <div class="viewer">
      <vc-viewer @ready="ready" style="overflow: hidden; position: absolute">
        <vc-overlay-html ref="html" :position="position1">
          <div class="vc-box">aa</div>
        </vc-overlay-html>
        <vc-entity :position="position1">
          <vc-graphics-point color="red" :pixel-size="8"></vc-graphics-point>
        </vc-entity>
        <vc-overlay-html :position="position2">
          <div class="vc-box">bb</div>
        </vc-overlay-html>
        <vc-entity :position="position2">
          <vc-graphics-point color="red" :pixel-size="8"></vc-graphics-point>
        </vc-entity>
        <vc-overlay-html :position="position3">
          <div class="vc-box">cc</div>
        </vc-overlay-html>
        <vc-entity :position="position3">
          <vc-graphics-point color="red" :pixel-size="8"></vc-graphics-point>
        </vc-entity>
        <!-- 弹窗效果 -->
        <vc-circle-roatating-double :radius1="15000" :radius2="30000" material1="./statics/SampleData/images/circle1.png" material2="./statics/SampleData/images/circle2.png" :position="positionModal" ref="circle"></vc-circle-roatating-double>
        <vc-entity :position="positionBox" ref="box">
          <vc-graphics-box :dimensions="dimensionsBox" :material="materialBox"></vc-graphics-box>
        </vc-entity>
        <vc-entity :position="positionModal">
          <vc-graphics-point color="red" :pixel-size="8"></vc-graphics-point>
        </vc-entity>
        <vc-overlay-html :position="positionModal" v-if="showModal" :pixel-offset="{ x: 0, y: -250 }">
          <div class="vc-dialog">
            <div class="line"></div>
            <div class="main">
              <div class="">风吹过已静下</div>
              <div class="">将心意再还谁</div>
            </div>
          </div>
        </vc-overlay-html>
        <vc-layer-imagery>
          <vc-provider-imagery-tianditu map-style="img_c" token="436ce7e50d27eede2f2929307e6b33c0"></vc-provider-imagery-tianditu>
        </vc-layer-imagery>
      </vc-viewer>
    </div>
  </template>
  
  
</doc-preview>
