        <span class="hljs-attr">position3</span>: { <span class="hljs-attr">lng</span>: <span class="hljs-number">117.386419</span>, <span class="hljs-attr">lat</span>: <span class="hljs-number">31.86446</span>, <span class="hljs-attr">height</span>: <span class="hljs-number">20</span> },
        <span class="hljs-attr">positionModal</span>: { <span class="hljs-attr">lng</span>: <span class="hljs-number">118.486419</span>, <span class="hljs-attr">lat</span>: <span class="hljs-number">32.86446</span>, <span class="hljs-attr">height</span>: <span class="hljs-number">40000</span> },
        <span class="hljs-attr">positionBox</span>: {},
        <span class="hljs-attr">dimensionsBox</span>: {},
        <span class="hljs-attr">materialBox</span>: {},
        <span class="hljs-attr">showModal</span>: <span class="hljs-literal">false</span>
      }
    },
    <span class="hljs-title function_">mounted</span>(<span class="hljs-params"></span>) {
      <span class="hljs-title class_">Promise</span>.<span class="hljs-title function_">all</span>([<span class="hljs-variable language_">this</span>.<span class="hljs-property">$refs</span>.<span class="hljs-property">circle</span>.<span class="hljs-property">createPromise</span>, <span class="hljs-variable language_">this</span>.<span class="hljs-property">$refs</span>.<span class="hljs-property">box</span>.<span class="hljs-property">createPromise</span>]).<span class="hljs-title function_">then</span>(<span class="hljs-function">() =&gt;</span> {
        <span class="hljs-keyword">const</span> { viewer } = <span class="hljs-variable language_">this</span>
        viewer.<span class="hljs-title function_">zoomTo</span>(viewer.<span class="hljs-property">entities</span>)
      })
    },
    <span class="hljs-attr">methods</span>: {
