        <span class="hljs-keyword">const</span> deviationHeight = <span class="hljs-number">400</span>
        <span class="hljs-keyword">const</span> heightMax = <span class="hljs-number">40000</span>
        <span class="hljs-variable language_">this</span>.<span class="hljs-property">positionBox</span> = <span class="hljs-function">() =&gt;</span> {
          height += deviationHeight
          <span class="hljs-keyword">if</span> (height &gt;= heightMax) {
            height = heightMax
          }
          <span class="hljs-keyword">return</span> <span class="hljs-title class_">Cesium</span>.<span class="hljs-property">Cartesian3</span>.<span class="hljs-title function_">fromDegrees</span>(positionModal.<span class="hljs-property">lng</span>, positionModal.<span class="hljs-property">lat</span>, height / <span class="hljs-number">2</span>)
        }
        <span class="hljs-variable language_">this</span>.<span class="hljs-property">dimensionsBox</span> = <span class="hljs-function">() =&gt;</span> {
          height += deviationHeight
          <span class="hljs-keyword">if</span> (height &gt;= heightMax) {
            height = heightMax
            <span class="hljs-variable language_">this</span>.<span class="hljs-property">showModal</span> = <span class="hljs-literal">true</span>
          }
          <span class="hljs-keyword">return</span> <span class="hljs-keyword">new</span> <span class="hljs-title class_">Cesium</span>.<span class="hljs-title class_">Cartesian3</span>(<span class="hljs-number">10000</span>, <span class="hljs-number">10000</span>, height)
        }
        <span class="hljs-variable language_">this</span>.<span class="hljs-property">materialBox</span> = <span class="hljs-title class_">Cesium</span>.<span class="hljs-property">Color</span>.<span class="hljs-property">DEEPSKYBLUE</span>.<span class="hljs-title function_">withAlpha</span>(<span class="hljs-number">0.5</span>)
        <span class="hljs-comment">// 仅用于调试</span>
        <span class="hljs-variable language_">window</span>.<span class="hljs-property">vm</span> = <span class="hljs-variable language_">this</span>
      }
    }
  }
</span><span class="hljs-tag">&lt;/<span class="hljs-name">script</span>&gt;</span>
<span class="hljs-tag">&lt;<span class="hljs-name">style</span> <span class="hljs-attr">scoped</span>&gt;</span><span class="language-css">
