<h2>属性</h2>
<!-- prettier-ignore -->
<table>
<thead>
<tr>
<th>属性名</th>
<th>类型</th>
<th>默认值</th>
<th>描述</th>
</tr>
</thead>
<tbody>
<tr>
<td>position</td>
<td>Object</td>
<td></td>
<td><code v-pre="">required</code> 指定 HTML 元素添加的位置。结构：{ lng: number, lat: number, height: number } 或者 Cesium.Cartesian3</td>
</tr>
<tr>
<td>pixelOffset</td>
<td>Number</td>
<td><code v-pre="">[0, 0]</code></td>
<td><code v-pre="">optional</code> 指定 HTML 元素像素偏移值。</td>
</tr>
<tr>
<td>hiddenOnBack</td>
<td>Number</td>
<td><code v-pre="">0.05</code></td>
<td><code v-pre="">optional</code> 指定 HTML 元素在地球背面时是否隐藏。</td>
</tr>
</tbody>
</table>
<hr>
<h2>事件</h2>
<table>
<thead>
<tr>
<th>事件名</th>
<th>参数</th>
<th>描述</th>
</tr>
</thead>
<tbody>
<tr>
<td>ready</td>
<td>{Cesium, viewer, cesiumObject}</td>
<td>该组件渲染完毕时触发，返回 Cesium 类, viewer 实例, 以及 HTML 对象。</td>
</tr>
<tr>
<td>click</td>
<td>event</td>
<td>鼠标点击该组件元素触发，事件鼠标参数。</td>
</tr>
</tbody>
</table>
