<style scoped="">
    .vc-box {
      width: 200px;
      line-height: 30px;
      background-color: rgba(0, 0, 0, 0.8);
      color: #fff;
      padding: 8px 16px;
    }
    .vc-dialog {
      /*重要*/
      user-select: none; /*禁止选中*/
      pointer-events: none; /*鼠标穿透*/
      top: 0;
      left: 0;
      width: 320px;
      height: 250px;
      z-index: 99999;
    }
    .vc-dialog .line {
      position: absolute;
      left: 0;
      width: 0;
      height: 100px;
      bottom: 0;
      background: url('/statics/SampleData/images/line.png');
